html {
  background-color: #076324;
}

/* General Overlay styles */

.overlay {
  -webkit-transition-duration: 1s;
       -o-transition-duration: 1s;
          transition-duration: 1s;
  z-index: 5;
  position: fixed;
  top: 100%;
  right: 0;
  height: 100%;
  width: 100%;
  background: lightgray;
}

.overlay .content {
  height: 100%;
  width: 100%;
  overflow: scroll;
}

.overlay .activate-tab {
  -webkit-transition-duration: 1s;
       -o-transition-duration: 1s;
          transition-duration: 1s;
  background: gray;
  position: absolute;
  height: 50px;
  top: -50px;
  right: 25px;
  padding: 0 25px;
  border-radius: 15px 15px 0 0;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
}

.overlay .activate-tab:hover {
  cursor: pointer;
}

.overlay.visible {
  top: 0;
}

.overlay.visible .activate-tab {
  top: 0;
  border-radius: 0 0 15px 15px;
}

/* Documentation Styles */

.documentation .content {
  margin: 25px;
}

.example .CodeMirror{
  height: auto;
}

/* Console styles */

.console {
  -ms-flex: 1 1;
      flex: 1 1;
  padding: 25px 10px;
}

.history {
  width: 100%;
  /* height: 100px; */
  background-color: lightgray;
}

.command {
  width: 100%;
  height: 300px;
  background-color: lightgray;
}

.console .history .CodeMirror {
  height: auto;
  max-height: 200px;
}

/* Freecell Game Styles */

.gameSection {
  -ms-flex: 1 1;
      flex: 1 1;
  padding: 25px 10px;
}

.gameTop {
  display: -ms-flexbox;
  display: flex;
  height: 170px;
}

.gameMain {
  display: -ms-flexbox;
  display: flex;
}

.freeCells {
  display: -ms-flexbox;
  display: flex;
  -ms-flex: 1 1;
      flex: 1 1;
}

.playedCards {
  display: -ms-flexbox;
  display: flex;
  -ms-flex: 1 1;
      flex: 1 1;
}

.column {
  position: relative;
  -ms-flex: 1 1;
      flex: 1 1;
  margin: 0 2px;
}

.freeCell {
  -ms-flex: 1 1;
      flex: 1 1;
  position: relative;
  margin: 0 2px;
}

.playedCardsColumn {
  -ms-flex: 1 1;
      flex: 1 1;
  position: relative;
  margin: 0 2px;
}

.cardRatioContainer {
  position: relative;
  width: 100%;
  height: 0;
  padding-top: 140%;
  background: inherit;
  border: 1px solid lightgray;
  border-radius: 4px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

.playedCards .cardRatioContainer .ace {
  position: absolute;
  top: 0;
  text-align: center;
  width: 100%;
  padding-top: 60%;
  font-size: 20px;
  opacity: .2;
}

.hideBorder .cardRatioContainer {
  border-color: transparent;
}

.column .cardRatioContainer {
  border: 1px solid transparent;
}

.greenBG .cardRatioContainer {
  background: green;
}

/* Card styles */

.card {
  position: absolute;
  top: 0;
  height: 100%;
  border-radius: 4px;
}

.card img {
  width: 100%;
}

.card.greenCard {
  background: green;
}

.card.greenCard img{
  opacity: .5;
}

.card.hideCard {
  opacity: 0;
}

.card.draggingCard {
  max-width: 75px;
  position: fixed;
  pointer-events: none;
  z-index: 2;
}



